import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-pricing',
  templateUrl: './pricing.component.html',
  styleUrls: ['./pricing.component.css']
})
export class PricingComponent implements OnInit {
  featuresArr:Array<string>;

  planObject:{planName:string,planAmount:number,planFeatures:Array<string>}[]=[];

  
  constructor() { }

  ngOnInit() {
  	this.getAllPlanDetails();
  }

  getAllPlanDetails(){
  	this.featuresArr=[];
  	this.featuresArr.push('Avail Services worth 1000','Dryer Sheet included','Fabric Softener included','Detergent included','Free Pickup & Delivery')
	this.planObject.push({planName:'SILVER',planAmount:999,planFeatures:this.featuresArr});
	this.planObject.push({planName:'GOLD',planAmount:1999,planFeatures:this.featuresArr});
	this.planObject.push({planName:'PLATINUM',planAmount:4999,planFeatures:this.featuresArr});
  }

}
