import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-banner',
  templateUrl: './banner.component.html',
  styleUrls: ['./banner.component.css']
})
export class BannerComponent implements OnInit {
	
   bannerImages:{imageName:string,imageActive:number}[]=[];
    constructor() { }

	ngOnInit() {
	  	this.getAllBanners();
	  }

	getAllBanners(){
		this.bannerImages.push({
			imageName:'banner.png',
			imageActive:1
		},
		{
			imageName:'banner.png',
			imageActive:0
		},
		{
			imageName:'banner.png',
			imageActive:0
		});
	}

}
